@import url(https://fonts.googleapis.com/css2?family=Source+Code+Pro&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App{font-family:'Source Code Pro', monospace;text-align:center}.App #quote-box{padding:25px;background-color:white;border-radius:10px;color:black}.App #quote-box #author{text-align:flex-end}.App #quote-box .buttons{margin:auto;display:block}.App #quote-box .buttons .button{border:none;border-radius:3px;color:#fff;background-color:#333;outline:none;font-size:0.85em;padding:8px 18px 6px 18px;margin-top:30px;opacity:1;cursor:pointer}.App #quote-box .buttons .button:hover{opacity:0.9}.App #quote-box .buttons #tweet-quote{display:flex;border-radius:10px;float:left;padding:10px;align-self:center;justify-content:center;color:white;text-decoration:none}.App #quote-box #new-quote{cursor:pointer;float:right;color:white;border-radius:10px;padding:10px;border:none;font-size:16px}.App .App-header{background-color:#282c34;min-height:100vh;display:flex;flex-direction:column;align-items:center;justify-content:center;font-size:calc(10px + 2vmin);color:white}@media (prefers-reduced-motion: no-preference){.App-logo{-webkit-animation:App-logo-spin infinite 20s linear;animation:App-logo-spin infinite 20s linear}}

